.layout {
	position: relative;
	display: flex;
	overflow: hidden;
	height: 100vh; }

.container {
	margin: 0 auto;
	padding: 0 15px; }

.main {
	position: relative;
	display: flex;
	flex-direction: column;
	flex: 1 1 auto;
	padding: 0 40px 0 0;
	overflow-y: auto;
	&_how-buy {
		justify-content: space-around; }
	&_news {
		padding: 160px 40px 210px; }
	&_history {
		padding: 100px 75px 20px 105px; }
	&_about-concept {
		padding: 100px 40px 20px; }
	&_about {
		padding: 100px 40px 20px; } }
